import React from "react";
import "./styles.css";

function matvec(v, M) {
  let result = [];
  for (let i = 0; i < M.length; i++) {
    let row = M[i];
    let terms = [];
    for (let j = 0; j < row.length; j++) {
      let M_item = row[j];
      let v_item = v[j];
      if (M_item && v_item) {
        for (let k = 0; k < v_item.length; k++) {
          terms.push(M_item + "Â·" + v_item[k]);
        }
      }
    }
    result.push(terms);
  }
  return result;
}

function computeTT(board, num) {
  let lhs = [board[0]];
  let rhs = board.map((row) => {
    let last = row[row.length - 1];
    return last ? [last] : null;
  });
  let tt = Array(num).fill(board);
  tt[0] = rhs;
  tt[num - 1] = lhs;
  return tt.reduce(matvec)[0];
}

class Cell extends React.Component {
  render() {
    let classNames =
      "cell cell-dropdown" +
      (this.props.isLastRole
        ? " cell-role-end"
        : this.props.isFirstRole
        ? " cell-role-start"
        : "");
    return (
      <select
        className={classNames}
        onChange={this.props.onChange}
        value={this.props.value}
      >
        <option value=""></option>
        <option value="1">1</option>
        <option value="sx">sx</option>
        <option value="sy">sy</option>
        <option value="sz">sz</option>
        <option value="c+">c+</option>
        <option value="c-">c-</option>
      </select>
    );
  }
}

class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      size: parseInt(this.props.boardSize, 10),
      board: this.emptyVMatrix(this.props.boardSize)
    };
    this.handlePlus = this.handlePlus.bind(this);
    this.handleMinus = this.handleMinus.bind(this);
  }

  emptyVMatrix(size) {
    let result = [];
    for (let i = 0; i < size; i++) {
      let r2 = [];
      result.push(r2);
      for (let j = 0; j < size; j++) {
        r2.push("");
      }
    }
    return result;
  }

  handleUpdate(i, j, e) {
    let val = e.target.value;
    let board = this.state.board;
    board[i][j] = val === "0" ? null : val;
    this.setState({
      board: board
    });
    this.notifyListeners(board);
  }

  notifyListeners(board) {
    if (this.props.handleUpdate) {
      this.props.handleUpdate(board);
    }
  }

  handlePlus() {
    let size = this.state.size + 1;
    let board = this.state.board.map((row) => {
      row.splice(-1, 0, "");
      return row;
    });
    let lrow = Array(size);
    lrow.fill("");
    board.splice(-1, 0, lrow);
    this.setState({
      size: size,
      board: board
    });
    this.notifyListeners(board);
  }

  handleMinus() {
    let size = this.state.size - 1;
    if (size > 0) {
      let board = this.state.board.map((row) => {
        row.splice(-2, 1);
        return row;
      });
      board.splice(-2, 1);
      this.setState({
        size: size,
        board: board
      });
      this.notifyListeners(board);
    }
  }

  render() {
    let board_items = [];
    let size = this.state.size;
    for (let i = 0; i < size; i++) {
      for (let j = 0; j < size; j++) {
        board_items.push(
          <Cell
            key={"c" + (i * size + j).toString()}
            onChange={this.handleUpdate.bind(this, i, j)}
            value={this.state.board[i][j]}
            isLastRole={j === size - 1}
            isFirstRole={i === 0}
          />
        );
      }
      if (i === 0) {
        board_items.push(
          <div className="cell cell-special" onClick={this.handlePlus} key="+">
            +
          </div>
        );
      } else if (i === 1) {
        board_items.push(
          <div className="cell cell-special" onClick={this.handleMinus} key="-">
            -
          </div>
        );
      }
      board_items.push(<div key={"eol" + i.toString()} className="clear" />);
    }
    this.boardItems = board_items;
    return <div className="board">{board_items}</div>;
  }
}

class MainPanel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      terms: [],
      board: undefined,
      len: undefined
    };
    this.handleUpdate = this.handleUpdate.bind(this);
    this.handleLenUpdate = this.handleLenUpdate.bind(this);
  }

  handleUpdate(board) {
    let newState = { board: board };
    if (this.state.len) newState["terms"] = computeTT(board, this.state.len);
    this.setState(newState);
  }

  handleLenUpdate(e) {
    let len = parseInt(e.target.value, 10);
    let newState = { len: len };
    if (this.state.board) newState["terms"] = computeTT(this.state.board, len);
    this.setState(newState);
  }

  render() {
    return (
      <div>
        <Board boardSize="2" handleUpdate={this.handleUpdate} />
        <input
          type="number"
          min="2"
          className="model-size-input"
          onChange={this.handleLenUpdate}
        />
        <div>
          {this.state.terms.map((value, index) => {
            return (
              <div key={index} className="product-term">
                {value}
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default function App() {
  return <MainPanel />;
}
